




























































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/sensorManage/api";
import AttrDetail from "./components/AttrEdit.vue";

@Component({
  components: {
    AttrDetail
  }
})
export default class SensorList extends Vue {
  searchDTO = {
    total: 0,
    current: 1,
    size: 10,
    query: {
      code: "",
      name: ""
    }
  };
  attrDialogTitle: string = "";
  attrDialogVisible: boolean = false;
  loading: boolean = false;
  attrList: Array<any> = [];
  attrData: any = {};
  indexCustom: number = 0;

  handleAdd() {
    this.attrDialogTitle = "新增传感器属性";
    this.attrDialogVisible = true;
    this.attrData = {};
  }

  handleEdit(data: any) {
    this.attrDialogTitle = "修改传感器属性";
    this.attrDialogVisible = true;
    this.attrData = JSON.parse(JSON.stringify(data));
  }

  handleDelete(id: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(`${api.deleteAttribute}${id}`).then((response: any) => {
          if (response.data.status) {
            this.$message.success(response.data.message);
            this.getAttrList();
          } else {
            this.$message.error(response.data.message);
          }
        });
      })
      .catch(() => {});
  }

  pageCurrentChange(current: number) {
    this.searchDTO.current = current;
    this.getAttrList();
  }

  pageSizeChange(size: number) {
    this.searchDTO.size = size;
    this.getAttrList();
  }

  getSearchList() {
    this.searchDTO.current = 1;
    this.getAttrList();
  }
  /**
   * 获取传感器属性列表
   * current：当前页
   * size：分页大小
   * query：查询参数
   */
  getAttrList() {
    this.loading = true;
    this.$http.post(api.getAttributePaging, this.searchDTO).then((res: any) => {
      this.loading = false;
      if (res.status) {
        if (res.data.status) {
          this.attrList = res.data.data.records;
          this.searchDTO.total = res.data.data.total;
          this.indexCustom = (res.data.data.current - 1) * res.data.data.size + 1;
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  submitAttrForm() {
    this.getAttrList();
    this.closeDialog();
  }

  closeDialog() {
    this.attrDialogVisible = false;
  }

  mounted() {
    this.getAttrList();
  }
}
